@import "../../base/variables.scss";

.pagination {
  font-size: 14px;
  line-height: 24px;
  width: 240px;
}

.pagination__pages {
  display: flex;
  font-size: 12px;
  line-height: 15px;

  &-item {
    display: inline-block;
    color: $color-ds50;
    width: 40px;
    height: 40px;
    line-height: 1.3;
    text-align: center;
    padding-top: 13px;

    &--active {
      color: #fff;
      font-weight: bold;
      background: $color-purple-gradient;
      border-radius: 22px;
    }

    &--arrow {
      color: #fff;
      line-height: 2;
      padding-top: 8px;
      background: $color-green-gradient;
      border-radius: 22px;
    }
  }
}

.pagination__description {
  margin-top: 8px;
  text-align: center;
}